import * as React from 'react';

import CssBaseline from '@mui/material/CssBaseline';
import Box from '@mui/material/Box';

import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';

import { ImageList, ImageListItem } from '@mui/material';

import Copyright from './components/copyright';
import ResponsiveAppBar from "./components/toolbarNew";
import AboutImage from './components/photos/7.jpg'
import AboutImage2 from './components/photos/8.jpg'
import CompanyName from './components/companyName';


const theme = createTheme();

document.title = "About Us - " + CompanyName;

export default function About() {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <ResponsiveAppBar />
      <main>
        {/* Hero unit */}
        <Box
          sx={{
            bgcolor: 'background.paper',
            pt: 0,
            pb: 0,
          }}
        >
          <Container maxWidth="xl">
            <h2>About Us</h2>
            <p>
                We’re lifelong dog lovers who have taken care of dogs (in our
                own home and theirs) for 20+ years. We have experience caring
                for multiple dogs at the same time, puppies being house trained,
                and elderly dogs who need daily medications. We provide frequent
                play sessions and fetch opportunities, constant snuggles and
                baths as needed. Our home will be a safe, fun and happy home
                away from home for your fur baby!
              </p>

              <p>
                Kevin and I both work from home so we’ll be around all day to
                keep your dog company and take them outside. We’re available for
                weekday or weekend bookings.
              </p>

              <p>
                We have a fully fenced yard that dogs LOVE! The fence is mainly
                chain link that is four-feet high in parts and 6 ft high in
                others. For most dogs, this works great.
              </p>
              <ImageList>
              <ImageListItem>
                <img src={AboutImage} alt="The McCalley Family" />
              </ImageListItem>
              <ImageListItem>
                <img src={AboutImage2} alt="The McCalley Family" />
              </ImageListItem>
            </ImageList>
              <p>
                We are a family with 2 kids (ages 3 and 5) who are in school
                part time. The kids are very respectful of dogs but we cannot
                accept any aggressive dogs at this time. We have a fenced acre
                around the house and a 10 year old Boston Terrier to keep your
                pets company. We have lots of room to roam and play and sniff
                and a nice private road for walks. Dogs are allowed to explore
                the yard as they please- except for the chicken coop. We have a
                doggy door providing access to a smaller fenced area which we
                can close off if needed.
              </p>
          </Container>
        </Box>
      </main>
      {/* Footer */}
      <Box sx={{ bgcolor: 'background.paper', p: 6 }} component="footer">
        <Typography
          variant="subtitle1"
          align="center"
          color="text.secondary"
          component="p"
        >
        </Typography>
        <Copyright />
      </Box>
      {/* End footer */}
    </ThemeProvider>
  );
}