import * as React from 'react';
import { List, ListItem, ListItemText, ListItemIcon } from '@mui/material';
import House from '@mui/icons-material/House';
import MedicationLiquid from '@mui/icons-material/MedicationLiquid';
import Accessibility from '@mui/icons-material/Accessibility';
import ElderlyWoman from '@mui/icons-material/ElderlyWoman';
import DirectionsRun from '@mui/icons-material/DirectionsRun';
import CssBaseline from '@mui/material/CssBaseline';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import Copyright from './components/copyright';
import ResponsiveAppBar from "./components/toolbarNew";




const theme = createTheme();

const services = [
  {
    name: 'Dog boarding',
    description: 'Overnight dog boarding in our home.',
    icon: <House />
  },
  {
    name: 'Oral Medication Administration',
    description: 'We have experience with getting your dog to take their medications.',
    icon: <MedicationLiquid />
  },
  {
    name: 'Special Needs Dog Experience',
    description: 'We will work to accommodate your dog\'s special needs',
    icon: <Accessibility />
  },
  {
    name: 'Senior Dog Experience',
    description: 'We have experience with elderly dogs and enjoy having them in our home.',
    icon: <ElderlyWoman />
  },
  {
    name: 'Can provide daily exercise',
    description: 'We have a large yard with a fence -- perfect for running and playing.',
    icon: <DirectionsRun />
  }
]

/*const ourHome = [
  {
    name: 'Single-Family One Story Home'
  },
  {
    name: 'Non-Smoking Household'
  },
  {
    name: 'Children 0-5 Years Old'
  },
  {
    name: 'Fenced Yard'
  }
]*/



export default function Services() {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <ResponsiveAppBar />
      <main>
        {/* Hero unit */}
        <Box
          sx={{
            bgcolor: 'background.paper',
            pt: 0,
            pb: 0,
          }}
        >
          <Container maxWidth="xl">
            <h2>Our Services</h2>
            <List>
            {services.map((service) => {
              return (
                <ListItem>
                  <ListItemIcon>
                    {service.icon}
                  </ListItemIcon>
                  <ListItemText primary={service.name} secondary={service.description} />
                </ListItem>
              );
            })}
            </List>
          </Container>
        </Box>
      </main>
      {/* Footer */}
      <Box sx={{ bgcolor: 'background.paper', p: 6 }} component="footer">
        <Typography
          variant="subtitle1"
          align="center"
          color="text.secondary"
          component="p"
        >
        </Typography>
        <Copyright />
      </Box>
      {/* End footer */}
    </ThemeProvider>
  );
}