import * as React from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
import Menu from "@mui/material/Menu";
import MenuIcon from "@mui/icons-material/Menu";
import Container from "@mui/material/Container";
import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import Tooltip from "@mui/material/Tooltip";
import MenuItem from "@mui/material/MenuItem";
import AvatarIcon from "./photos/7.jpg";
import PetsIcon from "@mui/icons-material/Pets";
import { createTheme, ThemeProvider } from "@mui/material";
import CompanyName from "./companyName";
import Link from "@mui/material/Link";
import { useNavigate } from "react-router-dom";

const theme = createTheme({
  palette: {
    primary: {
      main: "#137679",
    },
    secondary: {
      main: "#17A3A7",
    },
  },
});

const menuItems = [
  {
    to: '/contact',
    name: 'Contact Us'
  },
  {
    to: '/photos',
    name: 'Photos'
  },
  {
    to: '/services',
    name: 'Services'
  },
  {
    to: 'https://www.rover.com/members/laura-kevin-m-country-stay-cation-for-your-pups/?service_type=overnight-boarding&start_date=09%2F27%2F2022&end_date=09%2F29%2F2022&frequency=onetime&pet_type=dog#availability',
    name: 'Check Availability'
  }
];


const links = [
  {
    to: '/about',
    name: 'About Us'
  }
];


const ResponsiveAppBar = () => {
  const [anchorElNav, setAnchorElNav] = React.useState(null);
  const [anchorElUser, setAnchorElUser] = React.useState(null);

  const handleOpenNavMenu = (event) => {
    setAnchorElNav(event.currentTarget);
  };
  const handleOpenUserMenu = (event) => {
    setAnchorElUser(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  const handleCloseUserMenu = () => {
    setAnchorElUser(null);
  };

  return (
    <ThemeProvider theme={theme}>
      <AppBar position="static">
        <Container maxWidth="xl">
          <Toolbar disableGutters>
            <PetsIcon sx={{ display: { xs: "none", md: "flex" }, mr: 1, color: "#F5C962" }} />
            <Typography
              variant="h6"
              noWrap
              component="a"
              href="https://kevinmccalley.github.io/dogboarding"
              sx={{
                mr: 2,
                display: { xs: "none", md: "flex" },
                fontWeight: 400,
                letterSpacing: ".1rem",
                color: "#F5C962",
                textDecoration: "none",
              }}
            >
              <CompanyName />
            </Typography>

            <Box sx={{ flexGrow: 1, display: { xs: "flex", md: "none" } }}>
              <IconButton
                size="large"
                aria-label="account of current user"
                aria-controls="menu-appbar"
                aria-haspopup="true"
                onClick={handleOpenNavMenu}
                color="inherit"
              >
                <MenuIcon />
              </IconButton>
              <Menu
                id="menu-appbar"
                anchorEl={anchorElNav}
                anchorOrigin={{
                  vertical: "bottom",
                  horizontal: "left",
                }}
                keepMounted
                transformOrigin={{
                  vertical: "top",
                  horizontal: "left",
                }}
                open={Boolean(anchorElNav)}
                onClose={handleCloseNavMenu}
                sx={{
                  display: { xs: "block", md: "none" },
                }}
              >
                {menuItems.map((link) => {
                  function TheLink() {
                    const navigate = useNavigate();
                    function handleClick() {
                      if (link.name === "Check Availability") { 
                        window.location.href=link.to;
                      } else {
                        navigate(link.to);
                      };
                    }
                    return (
                      <Link onClick={handleClick} underline="none">
                        <MenuItem key={link.to} onClick={handleCloseUserMenu}>
                          <Typography textAlign="center">
                            {link.name}
                          </Typography>
                        </MenuItem>
                      </Link>
                    );
                  }
                  return(
                    <TheLink key={link.to} />
                  );
                })}
              </Menu>
            </Box>
            <PetsIcon sx={{ display: { xs: "flex", md: "none" }, mr: 1, color:"#F5C962" }} />
            <Typography
              variant="h5"
              noWrap
              component="a"
              href=""
              sx={{
                mr: 2,
                display: { xs: "flex", md: "none" },
                flexGrow: 1,
                fontSize: {xs: "13px"},
                fontWeight: 500,
                letterSpacing: ".1rem",
                color: "#F5C962",
                textDecoration: "none",
              }}
            >
              <CompanyName />
            </Typography>
            <Box sx={{ flexGrow: 1, display: { xs: "none", md: "flex" } }}>
            {menuItems.map((link) => {
                  function TheLink() {
                    const navigate = useNavigate();
                    function handleClick() {
                      if (link.name === "Check Availability") { 
                        window.location.href=link.to;
                      } else {
                        navigate(link.to);
                      };
                    }
                    return (
                      <Link onClick={handleClick} underline="none">
                        <Button key={link.to} onClick={handleCloseUserMenu} sx={{ my: 2, color: "white", display: "block" }}>
                            {link.name}
                        </Button>
                      </Link>
                    );
                  }
                  return(
                    <TheLink key={link.to} />
                  );
                })}
            </Box>

            <Box sx={{ flexGrow: 0 }}>
              <Tooltip title={<CompanyName />}>
                <IconButton onClick={handleOpenUserMenu} sx={{ p: 0 }}>
                  <Avatar alt="Laura and Kevin" src={AvatarIcon} />
                </IconButton>
              </Tooltip>
              <Menu
                sx={{ mt: "45px" }}
                id="menu-appbar"
                anchorEl={anchorElUser}
                anchorOrigin={{
                  vertical: "top",
                  horizontal: "right",
                }}
                keepMounted
                transformOrigin={{
                  vertical: "top",
                  horizontal: "right",
                }}
                open={Boolean(anchorElUser)}
                onClose={handleCloseUserMenu}
              > 
                {links.map(link => {
                  function TheLink() {
                    const navigate = useNavigate();
                    function handleClick() {
                      navigate(link.to);
                    }
                    return (
                      <Link onClick={handleClick} underline="none">
                        <MenuItem key={link.to} onClick={handleCloseUserMenu}>
                          <Typography textAlign="center">
                            {link.name}
                          </Typography>
                        </MenuItem>
                      </Link>
                    );
                  }
                  return(
                    <TheLink key={link.to} />
                  );
                })}
              </Menu>
            </Box>
          </Toolbar>
        </Container>
      </AppBar>
    </ThemeProvider>
  );
};
export default ResponsiveAppBar;
