import * as React from "react";
import {
  Box,
  Button,
  Container,
  CssBaseline,
  Stack,
  ImageListItem,
  ImageList,
  Typography,
} from "@mui/material";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import HomeImage from "./components/photos/3.jpg";
import HomeImage2 from "./components/photos/13.jpg";
import RoverImage from "./images/rover.png";
import Copyright from "./components/copyright";
import ResponsiveAppBar from "./components/toolbarNew";
import { useNavigate } from "react-router-dom";
import CompanyName from "./components/companyName";

function ContactButton() {
  const navigate = useNavigate();
  function handleClick() {
    navigate("/contact");
  }
  return (
    <Button variant="outlined" onClick={handleClick}>
      Contact Us
    </Button>
  );
}

function AvailabilityButton() {
  function handleClick() {
    window.location.replace(
      "https://www.rover.com/members/laura-kevin-m-country-stay-cation-for-your-pups/?service_type=overnight-boarding&start_date=09%2F27%2F2022&end_date=09%2F29%2F2022&frequency=onetime&pet_type=dog#availability"
    );
  }
  return (
    <Button variant="outlined" onClick={handleClick}>
      Availability
    </Button>
  );
}

<Copyright />;

const theme = createTheme({
  palette: {
    primary: {
      main: "#137679",
    },
    secondary: {
      main: "#17A3A7",
    },
  },
});

document.title = CompanyName;

export default function Home() {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <ResponsiveAppBar />
      <main>
        {/* Hero unit */}
        <Box
          sx={{
            bgcolor: "background.paper",
            pt: 2,
            pb: 0,
          }}
        >
          <Container maxWidth="xl">
            <Typography
              component="h4"
              variant="h4"
              align="center"
              color="text.primary"
              gutterBottom
            >
              Dog-sitting at our home in Morro Bay, CA.
            </Typography>

            <ImageList>
              <ImageListItem>
                <img src={HomeImage} alt="Your pet's vacation awaits!" />
              </ImageListItem>
              <ImageListItem>
                <img src={HomeImage2} alt="Your pet's vacation awaits!" />
              </ImageListItem>
            </ImageList>

            <Typography
              variant="h6"
              align="center"
              color="text.secondary"
              paragraph
            >
              <h3>A Country Stay-cation For Your Pups!</h3>
              <p>
                We are Laura and Kevin and we’re here to help you with your
                furry friends! We are looking forward to helping out as you
                travel.
              </p>
              <p>
                We're on
                <br />
                <a href="https://www.rover.com/members/laura-kevin-m-country-stay-cation-for-your-pups/"><img src={RoverImage} alt="rover.com" width="100" /></a>
              </p>
            </Typography>
            <Stack
              sx={{ pt: 4 }}
              direction="row"
              spacing={2}
              justifyContent="center"
            >
              <AvailabilityButton />
              <ContactButton />
            </Stack>
          </Container>
        </Box>
        <Box
          sx={{
            bgcolor: "background.paper",
            pt: 8,
            pb: 6,
          }}
        ></Box>
      </main>
      {/* Footer */}
      <Box sx={{ bgcolor: "background.paper", p: 6 }} component="footer">
        <Copyright />
      </Box>
      {/* End footer */}
    </ThemeProvider>
  );
}
